import offline from './pages/offline/index'
export default {
  name: 'app',
  data () {
    return {
      isOffline: false
    }
  },
  created () {
    window.addEventListener('load', () => {
      window.addEventListener('online', () => {
        this.isOffline = false
      })
      window.addEventListener('offline', () => {
        this.isOffline = true
      })
    })
  },
  computed: {
  },
  methods: {
    onlineFn () {
      this.isOffline = false
    }
  },
  components: {
    offline
  }
}



